<template>
  <div class="firstStep-main">
    <progress-bar :roleName="roleName"></progress-bar>
    <div>
      上傳圖片
    </div>
    <div class="firstStep-item">
      <c-cell title="预约查勘时间" rightType="text" :fontBold="800" :value="get_investigativeInformationDto.appointTimeObj.value"></c-cell>
      <c-cell @clickSelect="clickSelect('accidentTypeArr','事故类型')" title="事故类型" rightType="select" :mark="true" :value="get_investigativeInformationDto.accidentTypeObj.value"></c-cell>
      <c-cell @clickSelect="clickSelect('accidentDepartmentArr','事故处理类型')" title="事故处理部门" rightType="select" :mark="true" :value="get_investigativeInformationDto.accidentDepartmentObj.value"></c-cell>
      <c-cell title="交警姓名" rightType="input" :mark="true" :value="get_investigativeInformationDto.policyNameObj.value"></c-cell>
      <c-cell title="交警电话" rightType="input" :mark="true" :value="get_investigativeInformationDto.policyTelObj.value"></c-cell>
      <c-cell title="交警队" rightType="input" :mark="true" :value="get_investigativeInformationDto.policyTeamObj.value"></c-cell>
      <c-cell @clickSelect="clickSelect('riskCauseArr','出险原因')" title="出险原因" rightType="select" :mark="true" :value="get_investigativeInformationDto.riskCauseObj.value"></c-cell>
      <c-cell @clickSelect="clickSelect('responsibilityTypeArr','责任类型/比例')" title="责任类型/比例" rightType="select" :mark="true" :value="get_investigativeInformationDto.responsibilityObj.value"></c-cell>
      <c-cell @clickSelect="clickSelect('surveyMethodArr','查勘方式')" title="查勘方式" rightType="select" :mark="true" :value="get_investigativeInformationDto.surveyMethodObj.value"></c-cell>
      <c-cell title="查勘地点" rightType="text" :mark="true" :value="get_investigativeInformationDto.policyTeamObj.value"></c-cell>
    </div>
    <van-popup v-model="selectShow" :closeOnClickOverlay="false" position="bottom">
      <van-picker
        :showToolbar="true"
        valueKey="codeName"
        :title="selectTitle"
        :columns="selectItem"
        @cancel="baseInfoCancel"
        @confirm="baseInfoConfirm"
      />
    </van-popup>
    <div class="firstStep-footer" @click="goSecondStep">
      下一步
    </div>
  </div>
</template>
<script>
import progressBar from './progressBar.vue'
import { time } from '@/js/time.js'
import { mapGetters,mapActions } from 'vuex'
export default {
  name: 'firstStep',
  components: {
    'progress-bar': progressBar
  },
  data() {
    return {
      roleName: '李哈哈',

      //下拉选择
      selectTitle: '',//下拉框标题
      selectItem: [],
      selectShow: false,
      baseInfoIndex: '', // 默认选择值
      selectList: {
        accidentTypeArr: [
          {
            codeName: '单车',
            code: '01'
          },{
            codeName: '多车',
            code: '02'
          },{
            codeName: '多方',
            code: '03'
          }
        ],
        accidentDepartmentArr: [
          {
            codeName: '保险公司处理',
            code: '01'
          },{
            codeName: '自行协商',
            code: '02'
          },{
            codeName: '交管正常处理',
            code: '03'
          },{
            codeName: '交警简易程序处理',
            code: '04'
          },{
            codeName: '其他',
            code: '05'
          }
        ],
        riskCauseArr: [
          {
            codeName: '碰撞',
            code: '01'
          },{
            codeName: '倾覆',
            code: '02'
          },{
            codeName: '玻璃破碎',
            code: '03'
          },{
            codeName: '车身划伤',
            code: '04'
          },{
            codeName: '盗抢',
            code: '05'
          }
        ],
        responsibilityTypeArr: [
          {
            codeName: '全责100%',
            code: '01'
          },{
            codeName: '主责90%',
            code: '02'
          },{
            codeName: '同责80%',
            code: '03'
          },{
            codeName: '次责70%',
            code: '04'
          },{
            codeName: '无0%',
            code: '05'
          }
        ],
        surveyMethodArr: [
          {
            codeName: '第一现场查勘',
            code: '01'
          },{
            codeName: '复勘',
            code: '02'
          },{
            codeName: '未查勘',
            code: '03'
          },{
            codeName: '第二现场查勘',
            code: '04'
          }
        ],
      }
    }
  },
  created() {
    this.$store.dispatch('change_investigativeInformationDto',{
      value: time(),
      key: 'appointTimeObj'
    })
  },
  computed: {
    ...mapGetters(['get_investigativeInformationDto'])
  },
  methods: {
    ...mapActions(['change_investigativeInformationDto']),
    //选择事故类型下拉框显示
    clickSelect(flag,name) {
      this.selectItem=this.selectList[flag];
      this.selectShow=true;
      this.selectTitle=name
    },
    //下拉框确定
    baseInfoConfirm(value) {
      switch (this.selectTitle) {
      case '事故类型':
        this.$store.dispatch('change_investigativeInformationDto',{
          value: value.codeName,
          key: 'accidentTypeObj'
        })
        break;
      case '事故处理类型':
        this.$store.dispatch('change_investigativeInformationDto',{
          value: value.codeName,
          key: 'accidentDepartmentObj'
        })
        break;
      case '出险原因':
        this.$store.dispatch('change_investigativeInformationDto',{
          value: value.codeName,
          key: 'riskCauseObj'
        })
        break;
      case '责任类型/比例':
        this.$store.dispatch('change_investigativeInformationDto',{
          value: value.codeName,
          key: 'responsibilityObj'
        })
        break;
      case '查勘方式':
        this.$store.dispatch('change_investigativeInformationDto',{
          value: value.codeName,
          key: 'surveyMethodObj'
        })
        break;
      }
      this.selectShow = false;
    },
    //下拉框取消
    baseInfoCancel() {
      this.selectShow = false
    },
    //跳转到第二步
    goSecondStep() {
      this.$router.push({ name: '进度二' })
    }
  }
}
</script>
<style scoped lang="scss">
.firstStep-main{
  width: 100%;
  margin-top: 10px;
}
.firstStep-item{
  width: 100%;
  background-color: $white;
  .cell-main:first-child::before{
    display:none
  }
}
.firstStep-footer{
  width: 100%;
  height: 49px;
  margin-top: 20px;
  background: linear-gradient(to right,$orange,$orange-dark);
  color: $white;
  line-height: 49px;
  text-align: center;
}
</style>
