export function time(beforetime,aftertime){
  let timestamp = Date.parse(new Date()) / 1000;
  if (beforetime) {
    timestamp-=beforetime;
  }
  if (aftertime) {
    timestamp-=aftertime;
  }
  let nowTime= new Date(timestamp);
  let year=nowTime.getFullYear();
  let month=nowTime.getMonth()+1;
  if (month<10) {
    month='0'+month
  }
  let day=nowTime.getDay();
  if (day<10) {
    day='0'+day
  }
  let hour = nowTime.getHours() < 10 ? '0' + nowTime.getHours() : nowTime.getHours();
  let minutes = nowTime.getMinutes() < 10 ? '0' + nowTime.getMinutes() : nowTime.getMinutes();
  return year + '-' + month + '-' + day + ' ' + hour + ':' + minutes;
}

export function isRealTime(_time) {
  if(!new Date(_time).getFullYear()) {
    return false
  }
  return true
}