<template>
  <div class="progressBar-main">
    <ul class="content" ref="progressBar">
      <li class="item">
      </li>
      <li class="item">
      </li>
      <li class="item">
      </li>
      <li class="item">
      </li>
      <li class="item">
      </li>
    </ul>
    <span class="message">
      {{ roleName }}您好！还有{{ notStepNum }}步完成任务，请继续加油哦！
    </span>
  </div>
</template>
<script>
export default{
  name: 'progressBar',
  props: {
    progressNum: {
      type: Array,
      default: () => [1]
    },
    roleName: {
      type: String,
      default: '李哈哈'
    },
  },
  data() {
    return {
      notStepNum: 5
    }
  },
  mounted() {
    this.getProgress()
  },
  methods: {
    getProgress() {
      for (let i in this.progressNum) {
        this.$refs.progressBar.children[i].className='item active';
      }
      this.notStepNum-=this.progressNum.length;
    }
  }
}
</script>
<style scoped lang="scss">
.active{
  background-color: $orange-dark!important;
  &::after{
    background-color: $orange-dark!important;
  }
}
.progressBar-main{
  width: 100%;
  margin-top: 10px;
  background-color: $white;
  .content{
    padding: 20px 0;
    display: flex;
    justify-content: space-around;
    .item{
      width: 12px;
      height: 12px;
      border-radius: 50%;
      background-color: $gray;
      position: relative;
      &:not(:last-child)::after{
        content: '';
        margin: 0 1%;
        width: 54px;
        height: 3px;
        position: absolute;
        background-color: $gray;
        top:5px;
        left: 20px;
      }
    }
  }
  .message{
    display: inline-block;
    margin-left: 8%;
    margin-bottom: 20px;
  }
}
</style>
